module.exports = "/* Add rounded corners to the top left and the top right corner of the image */\nimg {\n    border-radius: 5px 5px 0 0;\n    width: 200px;\n    height: 200px;\n}\n/* Add some padding inside the card container */\n.container {\n    padding: 2px 16px;\n    display: -webkit-box;\n}\n.fondo{\n\n    background-image: url('slider-bg-2.jpg');\n    margin-top: -60px;\n  }\n.box4{position:relative; padding: 10px;}\n.box4:before{width:0;height:200%;background:rgba(0,0,0,.5);position:absolute;top:0;left:-250px;bottom:0;-webkit-transform:skewX(-36deg);transform:skewX(-36deg);transition:all .5s ease 0s}\n.box4:hover:before{width:200%}\n.box4 img{width:100%;}\n.box4:hover img{width:100%; -webkit-filter: blur(5px); filter: blur(5px)}\n.box4 .box-content{width:100%;height:100%;padding-top:10%;position:absolute;top:0;left:0;-webkit-transform:scale(0);transform:scale(0);transition:all .3s ease 0s;text-align: center;}\n.box4 .icon,.box5 .icon{list-style:none;padding:0}\n.box4:hover .box-content{-webkit-transform:scale(1);transform:scale(1)}\n.box4 .title{font-size:22px;font-weight:700;color:rgb(168, 5, 5);margin:0 0 10px; }\n.box4 .post{display:block;font-size:15px;font-weight:600;color:rgb(8, 0, 0);margin-bottom:20px; text-align: center}\n.box4 .icon{margin:0}\n.box4 .icon li{display:inline-block}\n.box4 .icon li a{display:block;width:35px;height:35px;line-height:35px;font-size:20px;background:#fff;color:#ee4266;margin-right:10px;transition:all .3s ease 0s}\n.box5 .icon,.box5 .icon li{display:inline-block}\n@media only screen and (max-width:990px){.box4{margin-bottom:30px}\n}\n@media only screen and (max-width:767px){.box4:before{left:-400px}\n.box4:hover:before{width:300%}\n}\n/*# sourceMappingURL=data:application/json;base64, */"